@import '../../css-config/Animation.scss';

.header {
    background: #fefefe;
    color: #030000;
    padding: 1rem;
    text-align: center;
    animation: fadeIn 2s ease-in-out;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    z-index: 1000; // Adjust the z-index as needed to ensure the header appears above other elements
  }

.logo {
  font-size: 24px;
  margin: 0;
}



.nav {
  margin-top: 10px;
  a {
    color: #360dee;
    text-decoration: none;
    margin: 0 10px;
    font-size: 16px;
  }
}


