.image-paragraph-container {
    margin: 80px 0;
    display: flex;
    flex-direction: column-reverse;
    // padding: 20px;
    animation: fadeInLeft 0.5s ease-in-out;
  
    @media (min-width: 768px) {
      flex-direction: row;
    }
  
    .image-container {
      order: 1;
      margin: auto;
  
      img {
        max-width: 100%;
        height: auto;
      }
  
      @media (min-width: 768px) {
        width: 100%;
        order: 0;
      }
    }
  
    .paragraph-container {
      order: 0;
      margin: auto;
  
      p {
        font-size: 18px;
        line-height: 2;
        color: rgba(0,0,0,0.5);
        text-align: justify;
      }
  
      @media (min-width: 768px) {
        width: 100%;
        order: 1;
      }
    }
  }
  
  @keyframes fadeInLeft {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  