@import "../../css-config/Animation.scss";
.footer {
    background-color: #333;
    color: #fff;
    padding: 20px;
    animation: fadeIn 2s ease-in-out;
    text-align: center;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .footer-logo {
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 3px;
    color: rgb(210, 195, 195);
    
  }
  
  .footer-links {
    display: flex;
  
    a {
      color: #fff;
      text-decoration: none;
      margin: 0 10px;
      transition: color 0.3s ease-in-out;
  
      &:hover {
        color: #3498db;
      }
    }
  }
  
  .footer-bottom {
    margin-top: 20px;
    p {
      margin: 0;
      font-size: 14px;
    }
  }
  